
import { HeroService } from '../../hero.service';
import { Hero } from '../../hero';
import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';


@Component({
  selector: 'app-hero-add',
  templateUrl: './hero-add.component.html',
  styleUrls: ['./hero-add.component.scss']
})
export class HeroAddComponent implements OnInit {
  heroForm: FormGroup;
  constructor(
    //private router: Router,
    private heroService: HeroService,
    private formBuilder: FormBuilder,
    //private sharedService: SharedService,
    private location: Location,) { }

  ngOnInit() {
    this.heroForm = this.formBuilder.group({
      id: ['', Validators.required],
      name: ['', [Validators.required, Validators.minLength(4)]],
      top: [false],
    });
  }
  onFormSubmit() {
    // heroForm.value即上面构建的Hero对象
    //this.heroService.addHero(this.heroForm.value);
     this.add(this.heroForm.get('id').value,this.heroForm.get('name').value,this.heroForm.get('top').value)
  }
  add(id: number, name: string, top: boolean): void {
    this.heroService.addHero({ id, name, top } as Hero)
    .subscribe(() => this.goBack());
    alert("保存成功")
  }

  goBack(): void {
    this.location.back();
  }
}
